	module.exports = {"data":{"markdownRemark":{"html":"<hr>\n<p>Welcome back, and happy new year!!</p>\n<p>I welcomed in the new year in a foreign country for the first time in my life for 2019. Birthright was a wonderful experience for me, and I really enjoyed it. Here's some highlights:</p>\n<p>I got to experience a connection to my Jewish heritage in a new way at the Western Wall:</p>\n<p><img src=\"https://s3.amazonaws.com/a-nerds-word/western_wall.jpg\" alt=\"alt text\" title=\"Western Wall\"></p>\n<p>I got to float in the Dead Sea:</p>\n<p><img src=\"https://s3.amazonaws.com/a-nerds-word/dead_sea_float.jpg\" alt=\"alt text\" title=\"Dead Sea\"></p>\n<p>I got to put on some healing mud from the lowest point on Earth:</p>\n<p><img src=\"https://s3.amazonaws.com/a-nerds-word/dead_sea_mud.jpg\" alt=\"alt text\" title=\"Dead Sea Mud\"></p>\n<p>I got to hike up Masada and visit an ancient Roman fort on the top of a mountain:</p>\n<p><img src=\"https://s3.amazonaws.com/a-nerds-word/masada.jpg\" alt=\"alt text\" title=\"Masada\"></p>\n<p>I got to ride a camel in the desert:</p>\n<p><img src=\"https://s3.amazonaws.com/a-nerds-word/camels.jpg\" alt=\"alt text\" title=\"Camels\"></p>\n<p>There were a bunch of other experiences that were meaningful to me, including visiting the Israeli Holocaust Museum. That's something that I think everyone should visit at some point, either in Israel or in the States (there's one in D.C.). It's an extremely emotional, harrowing experience, and it really gives an entirely different perspective into one of the darkest points in recent human history, one that you can't get just from a textbook.</p>\n<p>Anyway, on a happier note, I'll be going to SCG Worcester next weekend to visit some of my friends from college I haven't seen since I graduated and play some competitive magic, so I'll see you then!</p>\n<p>Till next time,</p>\n<p>-- Bryan</p>","frontmatter":{"date":"January 05, 2019","path":"/birthright","title":"Birthright","tags":["Event","Life","Personal"]}}},"pathContext":{}}

